<template>
    <div class="page-wrapper">

      <Sidebar/>
      <div class="main-container">

        <Navbar page = "Secretaria / Estudantes Matriculados"/>
        <div class="content-wrapper-scroll">

          <div class="content-wrapper">

            <div class="row">
              <div class="col-12">
                <div class="card">
                  <div class="card-body">
                      <div class="row mb-2">
                          <div class="col-md-4">
                            <label>Selecione o Ano</label><br>
                            <select
                              class="form-select"
                              v-model = "filter_data.ano_id"
                              >
                                <option value="">Todos os Anos</option> <option
                                    v-for = "ano in anos"
                                    :key = "ano.id"
                                    :value = "ano.id"
                                    >
                                {{ano.nome}}
                                </option>
                            </select>
                          </div>
                          <div class="col-md-4">
                            <label>Selecione o Curso</label><br>
                            <select
                              class="form-select"
                              v-model = "filter_data.curso_id"
                              @change="getSalas"
                              >
                                <option value="">Todos os Cursos</option> <option
                                    v-for = "curso in cursos"
                                    :key = "curso.id"
                                    :value = "curso.id"
                                    >
                                {{curso.nome}}
                                </option>
                            </select>
                          </div>
                           <div class="col-md-4">
                            <label>Selecione o Periodo</label><br>
                            <select
                              class="form-select"
                              v-model = "filter_data.periodo_id"
                              @change="getSalas"
                              >
                                <option value="">Todos os Perﾃｭodos</option> <option
                                    v-for = "periodo in periodos"
                                    :key = "periodo.id"
                                    :value = "periodo.id"
                                    >
                                {{periodo.nome}}
                                </option>
                            </select>
                          </div>
                          <div class="col-md-4">
                            <label>Selecione a Classe</label><br>
                            <select
                              class="form-select"
                              v-model = "filter_data.classe_id"
                              @change = "getSalas"
                              >
                                <option value="">Todas as Classes</option> <option
                                    v-for = "classe in classes"
                                    :key = "classe.id"
                                    :value = "classe.id"
                                    >
                                {{classe.nome}}
                                </option>
                            </select>
                          </div>
                          <div class="col-md-4">
                            <label>Selecione a Turma</label><br>
                            <select
                              class="form-select"
                              v-model = "filter_data.sala_id"
                              >
                                <option value="">Todas as Turmas</option> <option
                                    v-for = "sala in salas"
                                    :key = "sala.id"
                                    :value = "sala.id"
                                    >
                                {{sala.nome}}
                                </option>
                            </select>
                          </div>
                          <div class = "col-md-4">
                            <div class = "d-flex">
                                <div>
                                    <label class="text-white">.</label><br>
                                     <button @click="getMatriculados" class = "btn btn-primary btn-sm mx-1 fw-bold d-flex align-items-center p-2 border-0 text-uppercase">
                                        <span style="font-size:22px" class = "fas fa-search mx-1"></span> Pesquisar
                                     </button>
                                </div>
                                <div>
                                    <label class="text-white">.</label><br>
                                     <button 
                                        @click="getEstudantes" 
                                        style = "color:black" 
                                        class = "btn btn-warning btn-sm mx-1 fw-bold d-flex align-items-center p-2 border-0 text-uppercase"
                                        data-bs-toggle = "modal"
                                        data-bs-target = "#registrationMultiple"
                                        >
                                         Matricular Vﾃ｡rios
                                     </button>
                                </div>
                            </div>
                          </div>
                      </div>
                      <DataTable 
                      ref="table"
                      :data = "matriculados" 
                      :columns = "columns" 
                      :options="{ select: false, language:language }"
                      class="display table table-striped table-bordered"
                      >
                        <thead>
                          <tr class = "bg-secondary">
                            <th>NOME</th>
                            <th>IDENTIFICAﾃﾃグ</th>
                            <th>TELEFONE</th>
                            <th>CURSO</th>
                          </tr>
                        </thead>
                      </DataTable>

                  </div>
                </div>
              </div>
            </div>
            <div class="modal fade" data-bs-backdrop = "static" id = "registrationMultiple">
                <div class="modal-dialog modal-xl rounded-0">
                    <div class="modal-content rounded-0">
                        <div class="modal-header">
                            <h4>Matricular Vﾃ｡rios Estudantes ({{ selected_estudantes.length }})</h4>
                            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div class="modal-body">
                            <div class="row">
                                <div class="col-md-6">
                                    <h5>Selecione os Estudantes</h5>
                                    <input 
                                            type="text"
                                            class="form-control mb-3"
                                            v-model="search_estudante"
                                            @input="filterEstudantesList" 
                                            placeholder="Pesquisar Estudantes (Nome ou Identificaﾃｧﾃ｣o)"
                                        >
                                     <div style="max-height: 300px; overflow-y: auto; border: 1px solid #eee; padding: 10px;">
                                         <table class="table table-striped table-sm">
                                             <thead>
                                                 <tr>
                                                     <th>Sel.</th>
                                                     <th>Nome</th>
                                                     <th>Identificaﾃｧﾃ｣o</th>
                                                 </tr>
                                             </thead>
                                             <tbody>
                                                <tr v-for="estudante in filtered_estudantes" :key="estudante.id">
                                                    <td>
                                                        <input 
                                                            type="checkbox" 
                                                            :value="estudante.id" 
                                                            v-model="selected_estudantes"
                                                        >
                                                    </td>
                                                    <td>{{ estudante.nome }}</td>
                                                    <td>{{ estudante.identificacao }}</td>
                                                </tr>
                                                 <tr v-if="estudantes.length === 0">
                                                     <td colspan="3" class="text-center">Nenhum estudante encontrado.</td>
                                                 </tr>
                                             </tbody>
                                         </table>
                                     </div>
                                </div>

                                <div class="col-md-6">
                                    <h5>Estudantes a Matricular ({{ selectedEstudantesList.length }})</h5>
                                    <div style="max-height: 300px; overflow-y: auto; border: 1px solid #007bff; padding: 10px;">
                                        <ul class="list-group">
                                            <li 
                                                v-for="estudante in selectedEstudantesList" 
                                                :key="estudante.id"
                                                class="list-group-item d-flex justify-content-between align-items-center py-1 px-2"
                                            >
                                                {{ estudante.nome }} 
                                                <span 
                                                     class="badge bg-danger rounded-pill" 
                                                     style="cursor: pointer;"
                                                     @click="removeSelected(estudante.id)"
                                                 >
                                                     X
                                                 </span>
                                            </li>
                                            <li v-if="selectedEstudantesList.length === 0" class="list-group-item text-center text-muted">
                                                Nenhum estudante selecionado.
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer border-0 pt-0">
                            <button class="btn btn-secondary rounded-0" data-bs-dismiss = "modal">Fechar</button>
                            <button 
                                @click="matricularMultiplos" 
                                :disabled="selected_estudantes.length === 0 || !filter_data.ano_id || !filter_data.curso_id || !filter_data.sala_id || !filter_data.periodo_id || !filter_data.classe_id"
                                class="btn btn-primary rounded-0"
                            >
                                Confirmar Matrﾃｭculas ( {{ selected_estudantes.length }} )
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            <div class="modal fade" data-bs-backdrop = "static" id = "cancelRegistration">
                <div class="modal-dialog rounded-0">
                    <div class="modal-content rounded-0">
                        <div class="modal-header">
                            <h4>{{title}}</h4>
                        </div>
                        <div class="modal-body">
                            <div class="row">
                                <p>
                                    Deseja **remover permanentemente** esta matrﾃｭcula de <b>{{matricula.Estudante ? matricula.Estudante.nome : 'o estudante'}}</b>? Esta aﾃｧﾃ｣o ﾃｩ irreversﾃｭvel.
                                </p>
                            </div>
                        </div>
                        <div class="modal-footer border-0 pt-0">
                            <button class="btn btn-secondary rounded-0" data-bs-dismiss = "modal">Fechar</button>
                            <button @click="removerMatricula" class="btn btn-danger rounded-0">Remover Matrﾃｭcula</button>
                        </div>
                    </div>
                </div>
            </div>

          </div>
          <Footer/>
          </div>
        </div>
    </div>
</template>

<script>
    import $ from 'jquery'; 
    import '../static';
    import Sidebar from '../components/Sidebar.vue';
    import Navbar from '../components/Navbar.vue';
    import Footer from '../components/Footer.vue';
    import { api, url, language } from '../../../helpers/api';
    import DataTable from 'datatables.net-vue3';
    import DataTablesCore from 'datatables.net';
    import Select from 'datatables.net-select';
    import { mapState } from 'vuex';
    
    // Assuma-se que o Bootstrap JS estﾃ｡ carregado globalmente ou importado
    // Se nﾃ｣o estiver importado globalmente, vocﾃｪ precisarﾃ｡ importar a API do Modal:
    // import * as bootstrap from 'bootstrap'; 

    DataTable.use(DataTablesCore);
    DataTable.use(Select);

    export default{
        name:"matricula_view",
        components:{Sidebar,Navbar,Footer,DataTable},
        data(){
          return{
              title:"Matrﾃｭcula",
              dataTable:null,
              selectedRow: null,
              matriculados:[],
              estudantes:[],
              selected_estudantes: [], // IDs dos estudantes selecionados
              anos:[],
              cursos:[],
              periodos:[],
              salas:[], // Usado para popular o select de Turma (Sala)
              classes:[],
              search_estudante: '',
              estados:["Estudando","Cancelada","Transita","Nﾃ｣o Transita"],
              matricula:{
                  id:null,
                  Estudante:{},
                  estudante_id:'',
              },
              filter_data:{
                  ano_id:'',
                  curso_id:'',
                  sala_id:'',
                  periodo_id:'',
                  classe_id:'',
              },
              columns: [
                  { 
                    data: null,
                    render:(data) => {
                      return data.Estudante === null ? "Nﾃ｣o definido" : data.Estudante.nome
                    }
                  },
                  { 
                    data: null,
                    render:(data) => {
                      return data.Estudante === null ? "Nﾃ｣o definido" : data.Estudante.identificacao
                    }
                  },
                  { 
                    data: null,
                    render:(data) => {
                      return data.Estudante === null ? "Nﾃ｣o definido" : data.Estudante.telefone
                    }
                  },
                  { 
                    data: null,
                    render:(data) => {
                      return `${data.Curso.nome}`
                    }
                  }
              ],
              language:language
          }
        },
        mounted() {
          this.getEstudantes();
          this.getAnos();
          this.getCursos();
          this.getPeriodos();
          this.getClasses();
          // Remoﾃｧﾃ｣o do getTurmas, pois getSalas preenche this.salas
        },
        methods:{
            // --- NOVO Mﾃ欝ODO AUXILIAR: Fechar Modal Bootstrap 5 (Corrige TypeError) ---
            hideBootstrapModal(id) {
                const modalElement = document.getElementById(id);
                // Tenta usar a API nativa do Bootstrap 5 (Requer a importaﾃｧﾃ｣o do Bootstrap JS)
                if (modalElement && window.bootstrap && window.bootstrap.Modal) {
                    const modalInstance = window.bootstrap.Modal.getInstance(modalElement) || new window.bootstrap.Modal(modalElement);
                    modalInstance.hide();
                } else {
                    console.warn(`Bootstrap Modal JS API nﾃ｣o encontrada ou modal #${id} nﾃ｣o existe. Tentando jQuery.`);
                    // Fallback jQuery (se o jQuery estiver funcionando para outros propﾃｳsitos, mas o modal JS nﾃ｣o)
                    if (typeof $ !== 'undefined' && $.fn.modal) {
                         $(`#${id}`).modal('hide');
                    }
                }
            },
            
            // Novo mﾃｩtodo para remover um estudante da lista de selecionados (Matrﾃｭcula Mﾃｺltipla)
            removeSelected(estudanteId) {
                const index = this.selected_estudantes.indexOf(estudanteId);
                if (index > -1) {
                    this.selected_estudantes.splice(index, 1);
                }
            },

            // --- Matrﾃｭcula Mﾃｺltipla (CORRIGIDO: Fechamento do Modal) ---
            async matricularMultiplos() {
                // Validaﾃｧﾃ｣o de filtros antes da matrﾃｭcula
                if (!this.filter_data.ano_id || !this.filter_data.curso_id || !this.filter_data.sala_id || !this.filter_data.periodo_id || !this.filter_data.classe_id) {
                    this.$swal.fire({
                        title: "Erro de Filtro",
                        icon: "error",
                        text: "Por favor, selecione **Ano, Curso, Perﾃｭodo, Classe e Turma** para realizar as matrﾃｭculas."
                    });
                    return;
                }
                
                if (this.selected_estudantes.length === 0) {
                     this.$swal.fire({
                         title: "Atenﾃｧﾃ｣o",
                         icon: "warning",
                         text: "Selecione pelo menos um estudante para matricular."
                     });
                     return;
                }

                const matriculas = this.selected_estudantes.map(estudante_id => ({
                    ano_id: this.filter_data.ano_id,
                    curso_id: this.filter_data.curso_id,
                    sala_id: this.filter_data.sala_id,
                    periodo_id: this.filter_data.periodo_id,
                    classe_id: this.filter_data.classe_id,
                    estudante_id: estudante_id,
                    user_id: this.user.id,
                    estado: "Estudando"
                }));
                
                const form = {
                    matriculas: matriculas,
                    user_id: this.user.id,
                    nome: this.user.nome
                };

                try {
                    const res = await api.post(`/matricula/multiple`, form);
                    
                    const successMessage = res.data.message || `${this.selected_estudantes.length} Matrﾃｭcula(s) realizada(s) com sucesso.`;

                    this.$swal.fire({
                        title: "Sucesso!",
                        icon: "success",
                        text: successMessage
                    });

                    this.selected_estudantes = [];
                    // 識 CORREﾃﾃグ: Usando a funﾃｧﾃ｣o nativa do Bootstrap para fechar o modal
                    this.hideBootstrapModal('registrationMultiple'); 
                    
                    this.getMatriculados();
                    
                } catch (err) {
                    console.error("ERRO COMPLETO na Matrﾃｭcula Mﾃｺltipla:", err);

                    const errorMessage = err.response?.data?.message || "Erro ao matricular estudantes. Verifique o console.";
                    this.$swal.fire({
                        title: "Erro na Matrﾃｭcula",
                        icon: "error",
                        text: errorMessage
                    });
                }
            },
            
            // --- Remover Matrﾃｭcula (CORRIGIDO: Fechamento do Modal) ---
            async removerMatricula(){
                try {
                    const form = {
                        user_id: this.user.id,
                        nome: this.user.nome
                    }
                    
                    const res = await api.delete(`/matricula/${this.matricula.id}`, { data: form });

                    this.$swal.fire({
                        title: res.data.message,
                        icon:"success"
                    });
                    
                    this.getMatriculados();
                    // 識 CORREﾃﾃグ: Usando a funﾃｧﾃ｣o nativa do Bootstrap para fechar o modal
                    this.hideBootstrapModal('cancelRegistration');
                    this.clearForm();
                    
                } catch (err) {
                    const errorMessage = err.response?.data?.message || "Erro: Nﾃ｣o foi possﾃｭvel excluir a matrﾃｭcula.";
                    this.$swal.fire({
                        title: errorMessage,
                        icon:"error"
                    });
                    console.error("Erro ao remover matrﾃｭcula:", err.response.data);
                }
            },
            
            // --- getSalas (CORRIGIDO: Limpeza do select aninhado) ---
            getSalas(){
              const { periodo_id, curso_id, classe_id } = this.filter_data;
              
              // Se um dos filtros upstream for limpo, limpa salas e a seleﾃｧﾃ｣o de sala
              if (!periodo_id || !curso_id || !classe_id) {
                  this.salas = []; 
                  this.filter_data.sala_id = ''; // Limpa a seleﾃｧﾃ｣o da Turma
                  return; 
              }

              api
              .get(`/salas/curso/${curso_id}/classe/${classe_id}/periodo/${periodo_id}`)
              .then(res => {
                  this.salas = res.data;
                  // Se a lista de salas carregar vazia, garante que sala_id estﾃ｡ limpo
                  if(this.salas.length === 0) {
                    this.filter_data.sala_id = '';
                  }
              })
              .catch(err => {
                  console.error("Erro ao carregar salas:", err)
                  this.salas = [];
                  this.filter_data.sala_id = '';
              })
            },
            generatePDF() {
              const {ano_id, curso_id, periodo_id, classe_id, sala_id} = this.filter_data;
              if (!ano_id || !curso_id || !periodo_id || !classe_id || !sala_id) {
                 this.$swal.fire({
                     title: "Filtros Incompletos",
                     icon: "warning",
                     text: "Por favor, selecione Ano, Curso, Perﾃｭodo, Classe e Turma antes de gerar o PDF."
                 });
                 return;
              }
              window.open(`${url}/api/documentos/lista-matriculados/ano/${ano_id}/curso/${curso_id}/periodo/${periodo_id}/classe/${classe_id}/sala/${sala_id}`, "_blank")
            },
            getMatriculados(){
              const {ano_id,sala_id} = this.filter_data;
              
              if (!ano_id || !sala_id) {
                 this.$swal.fire({
                     title: "Filtros Obrigatﾃｳrios",
                     icon: "warning",
                     text: "Por favor, selecione o Ano e a Turma para pesquisar os estudantes matriculados."
                 });
                 this.matriculados = [];
                 return;
              }

              api
              .get(`/matricula/ano/${ano_id}/turma/${sala_id}/`)
              .then(res => {
                  if(res.data && res.data.length > 0){
                    this.matriculados = res.data;
                  }else{
                    this.matriculados = []
                       this.$swal.fire({
                          title:"Notificaﾃｧﾃ｣o",
                          icon:"warning",
                          text:"Nenhum Estudante Matriculado Encontrado com os filtros selecionados."
                        })
                  }
                  this.clearForm(); // Limpa a seleﾃｧﾃ｣o de linha apﾃｳs a pesquisa
              })
              .catch(err => {
                  console.log(err)
                  this.matriculados = [];
              })
            },
            getEstudantes(){
              api
              .get(`/estudantes/`)
              .then(res => {
                  this.estudantes = res.data;
                  // Reseta a seleﾃｧﾃ｣o de estudantes ao reabrir o modal (melhor UX)
                  this.selected_estudantes = []; 
                  this.search_estudante = '';
              })
              .catch(err => {
                  console.log(err)
              })
            },
            getAnos(){
              api.get(`/anos/status/1/`).then(res => { this.anos = res.data; }).catch(err => { console.log(err) })
            },
            getCursos(){
              api.get(`/cursos/`).then(res => { this.cursos = res.data; }).catch(err => { console.log(err) })
            },
            getClasses(){
              api.get(`/classes/`).then(res => { this.classes = res.data; }).catch(err => { console.log(err) })
            },
            getPeriodos(){
              api.get(`/periodos/`).then(res => { this.periodos = res.data; }).catch(err => { console.log(err) })
            },
            // --- CORREﾃﾃグ: Lﾃｳgica para seleﾃｧﾃ｣o de linha na DataTables ---
            selectRow() {
                const table = this.$refs.table.dt;
                
                // Limpa o registro selecionado.
                this.matricula = { id: null, Estudante: {} };

                // Obtﾃｩm os dados da primeira linha selecionada.
                const selectedData = table.rows({ selected: true }).data();

                if (selectedData.length > 0) {
                    this.matricula = selectedData[0];
                }
            },
            clearForm(){
                // Limpa a variﾃ｡vel 'matricula' e desseleciona a linha na tabela.
                this.matricula = { id:null, Estudante:{}, estudante_id:'' };
                if (this.$refs.table && this.$refs.table.dt) {
                    this.$refs.table.dt.rows().deselect();
                }
            },
        },
        // --- PROPRIEDADE COMPUTADA ---
        computed:{
            ...mapState(['user']), 
            selectedEstudantesList() {
                // Mapeia os IDs selecionados para os objetos de estudante correspondentes
                return this.estudantes.filter(estudante => 
                    this.selected_estudantes.includes(estudante.id)
                );
            },
            // NOVO COMPUTED PROPERTY para a pesquisa no modal
            filtered_estudantes() {
                const searchTerm = this.search_estudante.toLowerCase().trim();
                
                if (!searchTerm) {
                    // Se nﾃ｣o houver termo de pesquisa, retorna todos os estudantes.
                    // NOTA IMPORTANTE: Vocﾃｪ pode querer verificar se o estudante jﾃ｡ estﾃ｡ em selected_estudantes aqui
                    // para nﾃ｣o listﾃ｡-los, mas para manter simples, vamos retornar todos.
                    return this.estudantes; 
                }
                
                return this.estudantes.filter(estudante => {
                    const nome = (estudante.nome || '').toLowerCase();
                    const identificacao = (estudante.identificacao || '').toLowerCase();
                    
                    // Retorna se o termo de pesquisa estiver no nome OU na identificaﾃｧﾃ｣o
                    return nome.includes(searchTerm) || identificacao.includes(searchTerm);
                });
            }
        }
    }

</script>

<style>
@import 'datatables.net-bs5';
.col-md-4 .d-flex {
    padding-top: 0.1rem; 
}
</style>